<template>
   <fieldset class="pa-3" style="width:100%">
      <legend>{{ $attrs.label }}</legend>
   
      <v-text-field outlined dense append-icon="mdi-plus" v-model="text" @click:append="add" @keydown.enter="add"></v-text-field>
      <v-expansion-panels v-if="items.length">
        <v-expansion-panel
          v-for="(item,ind) in items"
          :key="ind"
        >
       
          <v-expansion-panel-header>
            <v-row class="pa-0 ma-0" style="width: 100%">
              <v-col cols="11" class="pa-0 ma-0"><h4><v-icon v-html="item.icon"></v-icon > {{ item.name }}</h4></v-col>
              <v-col class="pa-0 ma-0"> <v-btn icon @click.stop="moveDown(ind)" v-if="ind < (items.length - 1)" small><v-icon class="tomato">mdi-arrow-down-thick</v-icon></v-btn></v-col>
              <v-col class="pa-0 ma-0"><v-btn icon @click.stop="moveUp(ind)" v-if="ind" small><v-icon class="tomato">mdi-arrow-up-thick</v-icon></v-btn></v-col>
            </v-row>
            
            <v-spacer></v-spacer>
                                 
                                  

          </v-expansion-panel-header>
          <v-expansion-panel-content>
             <v-text-field outlined dense v-model="item.name" label="Name"></v-text-field>
        
             <v-text-field outlined dense v-model="item.icon" label="Icon" :append-icon="item.icon"></v-text-field>
             <v-text-field outlined dense v-model="item.path" label="Path"></v-text-field>
            <v-btn icon @click="remove(ind)"> <v-icon>mdi-delete </v-icon></v-btn>
          </v-expansion-panel-content>
        </v-expansion-panel>
      </v-expansion-panels>
      <v-sheet v-else  color="grey lighten-5 pa-6">Menu list is empty!</v-sheet>
    </fieldset>
</template>

<script>
export default {
  name: 'MenuItemInput',
  props: {
    value: Array
  },
  data () {
    return {
      text: '',
      items: [ ]
    }
  },
  watch: {
    items: {
      handler: 'input',
      deep: true
    }
  },
  methods: {
    moveUp (index) {
      this.move(this.items, index, index-1);
    },
    moveDown(index) {
      this.move(this.items, index, index+1);
    },
    move(arr, old_index, new_index) {
      if (new_index >= arr.length) {
          var k = new_index - arr.length + 1;
          while (k--) {
              arr.push(undefined);
          }
      }
      arr.splice(new_index, 0, arr.splice(old_index, 1)[0]);
      return arr; // for testing
    },
    input () {
      this.$emit('input', this.items)
    },
    remove(i) {
      if (this.items.length)
        this.items = this.items.filter((e,ind) => ind !== i)
    },
    add () {
 
      if (this.text) 
        this.items.push({name:this.text, icon:'mdi-home', path: '/'})
      this.text = ''
    }
  },
  created () {
    this.items = this.value
  }
}
</script>
